// ** React Imports
import { Fragment, useEffect, useState, useRef } from 'react'
import toast from 'react-hot-toast'
import axios from '@src/configs/axios/axiosConfig'
// ** Reactstrap Imports
import {
  Card,
  Input,
  Button,
  CardBody,
  Label,
  ListGroup,
  ListGroupItem,
  Col,
  Row,
  Progress,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Form
} from 'reactstrap'

// ** Third Party Components
import 'cleave.js/dist/addons/cleave-phone.us'
import { X, Paperclip, Plus, MoreVertical, Trash } from 'react-feather'
import { addTaskRequirements, getTaskRequirements, updateTaskRequirements, deleteTaskRequirements } from '../store/taskrequirements'
import { useDispatch, useSelector } from 'react-redux'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import { activeOrganizationid, activeOrganization } from '@src/helper/sassHelper'

import { useParams } from 'react-router-dom'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'

import { useForm } from 'react-hook-form'
const activeOrgId = activeOrganizationid()
const activeOrg = activeOrganization()
const userId = activeOrg?.orguserid
const MySwal = withReactContent(Swal)
// ** Images
const TaskRequirements = props => {
  // ** Hook
  const { tabId, selectedTask } = props

  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  //** State */
  const { id } = useParams()
  const [itemList, setItemList] = useState([])
  const [filesArr, setFilesArr] = useState([])
  const [comment, setComment] = useState('')
  const [addFlag, setAddFlag] = useState(false)
  const [editReqId, setEditReqId] = useState(null)
  const [selItemId, setSelItemid] = useState(null)
  const [attachmentId, setAttachmentId] = useState(null)
  const dispatch = useDispatch()
  const store = useSelector(state => state.taskrequirements)

  const { handleSubmit } = useForm()

  const hiddenFileInput = useRef(null)

  const editItem = (id) => {
    const temp = itemList.map((obj) => {
      if (obj.id === id) {
        setComment(obj?.title)
        setEditReqId(obj?.id)
        return { ...obj, editFlag: true }
      } else {
        return { ...obj, editFlag: false }
      }

    })
    setAddFlag(false)
    setItemList(temp)
  }

  const cancelItem = () => {
    const temp = itemList.map((obj) => {
      return { ...obj, editFlag: false }
    })

    setItemList(temp)
  }

  const configUpload =
  {
    headers: {
      "Content-Type": "multipart/form-data"
    },
    onUploadProgress: data => {
      console.log(data)
      //Set the progress value to show the progress bar
      // setProgress(Math.round((100 * data.loaded) / data.total))
    }
  }

  const addAttachment = () => {

    axios.post('/taskrequirements/addattachments', { attachmentIds: attachmentId, id: selItemId, updatedBy: userId })
      .then(() => {
        toast.success('Added successfully', { position: 'bottom-right' })
      }).catch(() => {
        toast.error('Upload Failed', { position: 'bottom-right' })
      })

  }

  useEffect(() => {
    if (attachmentId && selItemId) {
      addAttachment()
    }
  }, [attachmentId, selItemId])

  const uploadFiles = (files) => {

    setBlock(true)
    const formData = new FormData()
    files.forEach((file, key) => {
      formData.append(`files[${key}]`, file)
    })

    formData.append('organizationId', activeOrgId)
    formData.append('createdBy', activeOrg.orguserid)
    formData.append('clientId', id)

    formData.append('folderPath[0]', localStorage.getItem('clientName'))
    formData.append('folderPath[1]', 'Task')
    formData.append('folderPath[2]', `${selectedTask.servicename}- ${selectedTask.uniqueno}`)

    axios.post('/files/upload', formData, configUpload).then((res) => {
      setAttachmentId(res.data.attachment_ids[0])
      setBlock(false)

    }).catch(() => {
      toast.error('Upload Failed', { position: 'bottom-right' })
      setBlock(false)
    })
  }


  const handleClick = id => {
    setSelItemid(id)
    hiddenFileInput.current.click()
  }

  const handleChange = async event => {
    const files = Array.from(event.target.files)

    const acceptedFiles = []
    files.forEach(obj => {
      obj.extension = obj.name.split('.').pop()
      acceptedFiles.push(obj)
    })

    await uploadFiles(acceptedFiles)
    setFilesArr([...filesArr, ...acceptedFiles])

  }

  const onEditSubmit = async () => {

    if (comment !== '') {
      const data = {
        updatedBy: userId,
        name: comment,
        taskId: id,
        id: editReqId
      }
      setBlock(true)
      await dispatch(updateTaskRequirements(data))
      toast.success('Updated Successfully', { position: 'bottom-right' })
      setComment('')
      setEditReqId(null)
      setBlock(false)
    }

  }

  const deleteRequirement = async (reqId) => {
    setBlock(true)
    await dispatch(deleteTaskRequirements({ id: reqId, taskId: id }))
    setBlock(false)
    toast.success('Deleted Successfully', { position: 'bottom-right' })
  }

  const onSubmit = async () => {

    if (comment !== '') {
      const data = {
        organizationId: activeOrgId,
        createdBy: userId,
        name: comment,
        taskId: id
      }
      setBlock(true)
      await dispatch(addTaskRequirements(data))
      toast.success('Added Successfully', { position: 'bottom-right' })
      setComment('')
      setBlock(false)
    }

  }

  useEffect(async () => {
    if (tabId === 'taskrequirments') {
      await dispatch(getTaskRequirements(id))
    }
  }, [tabId])

  useEffect(() => {
    setItemList(store?.data)
  }, [store?.data])

  const addItem = () => {
    setAddFlag(true)
  }

  const cancelAddItem = () => {
    setAddFlag(false)
  }

  return (
    <Fragment>
      <UILoader blocking={block} loader={<Loader />}>
        <Card>
          <CardBody className={'p-1'}>

            {itemList?.map((item, key) => {
              return (
                <Col key={key} className={'d-flex justify-content-between align-items-center mb-50'}>
                  <Col lg={8} className={'d-flex align-items-top'}>
                    <Input type='checkbox' defaultValue={item.id} />
                    {
                      item.editFlag &&
                      <Form onSubmit={handleSubmit(onEditSubmit)}>
                        <Col>
                          <Input type='text' className='ms-50' value={item.editFlag ? comment : item.title} onChange={(e) => setComment(e.target.value)} />
                          <Col className={'mt-75 ms-50'}>
                            <Button size='sm' color='primary' type='submit'>Save</Button>
                            <Button size='sm' color='warning' className='ms-50' outline onClick={() => cancelItem()}>Cancel</Button>
                          </Col>
                        </Col>
                      </Form>
                    }
                    {
                      !item.editFlag && <Label className={'ms-1'} onClick={() => { editItem(item.id) }}>{item.title}</Label>
                    }
                  </Col>
                  {!item.editFlag &&
                    <Col lg={4} className={'d-flex text-end justify-content-end'}>
                      <input type='file' multiple className='hidden' onChange={handleChange} ref={hiddenFileInput} />
                      <Button type='button' color='primary' size='sm' onClick={() => handleClick(item.id)} >
                        Choose File
                      </Button>

                      <UncontrolledDropdown>
                        <DropdownToggle tag='div' className='btn btn-sm'>
                          <MoreVertical size={14} className='cursor-pointer' />
                        </DropdownToggle>
                        <DropdownMenu>

                          <DropdownItem
                            tag='a'
                            href='/'
                            className='w-100 py-0'
                            onClick={e => {
                              e.preventDefault()
                              deleteRequirement(item?.id)
                            }}
                          >
                            <Trash size={12} className='me-50' />
                            <span className='align-middle font-small-3'>Delete</span>
                          </DropdownItem>

                        </DropdownMenu>
                      </UncontrolledDropdown>
                    </Col>
                  }
                </Col>
              )
            })
            }
            {addFlag &&
              <Form onSubmit={handleSubmit(onSubmit)}>
                <Col lg={12} className={'py-75'} >
                  <Input type='text' value={comment} onChange={(e) => setComment(e.target.value)} />
                  <Col className={'mt-75'}>
                    <Button size='sm' color='primary' type='submit'>Add</Button>
                    <Button size='sm' color='warning' className='ms-50' outline onClick={() => cancelAddItem()}>Cancel</Button>
                  </Col>
                </Col>
              </Form>
            }
            <Col>
              <Button size='sm' color={'primary'} onClick={() => { addItem() }}>
                <Plus size='14' />Add Item
              </Button>
            </Col>
          </CardBody>
        </Card>
      </UILoader>
    </Fragment >
  )
}

export default TaskRequirements
