// ** Custom Components
import Timeline from '@components/timeline'

// ** Reactstrap Imports
import { Card, CardBody, CardHeader, CardTitle, Row } from 'reactstrap'
import axios from '@src/configs/axios/axiosConfig'
// ** Timeline Data

import { useEffect, useState } from 'react'
import moment from 'moment'
import { User } from 'react-feather'

const Attachment = ({ files }) => {


  const filePath = {
    pdf: require(`@src/assets/images/icons/pdf.png`).default,
    jpg: require(`@src/assets/images/icons/jpg.png`).default,
    jpeg: require(`@src/assets/images/icons/jpg.png`).default,
    png: require(`@src/assets/images/icons/jpg.png`).default,
    xlsx: require(`@src/assets/images/icons/xls.png`).default,
    docx: require(`@src/assets/images/icons/jpg.png`)

  }

  return (
    <Row>{
      files?.map((file, k) => {
        return (
          <div className='d-flex align-items-center' key={k}>
            <div className={`ui-attachment-box`}>
              <img src={filePath[file.extension]} />
              <p className='attachemntFilename'>{file?.originalfilename}</p>
            </div>
          </div>
        )
      })
    }
    </Row>
  )
}

const Activity = ({ tabId, selectedTask }) => {

  const [activityList, setActivityList] = useState([])

  const getActivity = () => {

    axios.post('/taskcomments/list', { taskId: selectedTask?.id })
      .then((res) => {

        const temp = res.data.comments?.map((item) => {
          const obj = {
            title: item?.title,
            content: item.content,
            meta: moment(item.createdon, 'x').fromNow(),
            icon: <User size={14} />,
            color: 'secondary',
            customContent: item.files ? <Attachment files={item.files} /> : null
          }
          return obj

        })

        setActivityList(temp)

      }).catch(() => {

      })
  }

  useEffect(() => {
    if (tabId === 'taskactivity') {
      getActivity()
    }

  }, [tabId])


  return (
    <Card>
      <CardHeader>

      </CardHeader>
      <CardBody>
        <Timeline data={activityList} />
      </CardBody>
    </Card>
  )
}

export default Activity
