// ** React Imports
import { Fragment, useEffect, useState } from 'react'
import { Link, useLocation } from 'react-router-dom'
// ** Reactstrap Imports
import { Row, Col, Card, CardBody, Badge, Table, Nav, NavItem, NavLink, TabContent, TabPane, CardHeader } from 'reactstrap'
// ** Third Party Components
import Activity from './Activity'
import TaskRequirements from './TaskRequirements'
import FinalDelivery from './FinalDelivery'


import '@styles/react/libs/react-select/_react-select.scss'

import moment from 'moment'
const UserInfoCard = ({ selectedTask }) => {


  const search = useLocation().search
  const searchParams = new URLSearchParams(search)
  const [active, setActive] = useState('taskactivity')
  // ** State

  const toggleTab = tab => {
    if (active !== tab) {
      setActive(tab)
    }
  }

  useEffect(() => {
    const tabId = searchParams.get('tabId')
    if (tabId) {
      setActive(tabId)
    }
  }, [searchParams])


  const dateFormat = (value) => {
    if (value.length === 13) {
      return moment(value, 'x').format("MMM DD, YYYY")
    } else {
      return moment.unix(value).format("MMM DD, YYYY")
    }
  }

  return (
    <Fragment>

      <Row>
        <Col lg={8}>
          <Card>
            <CardBody>
              <Nav tabs className="flex-lg-row flex-column nav nav-left">
                <NavItem>
                  <NavLink active={active === 'taskactivity'} onClick={() => toggleTab('taskactivity')}>
                    <span className='fw-bold'>Activity</span>
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink active={active === 'taskrequirments'} onClick={() => toggleTab('taskrequirments')}>
                    <span className='fw-bold'>Task Requirements</span>
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink active={active === 'taskfinal'} onClick={() => toggleTab('taskfinal')}>
                    <span className='fw-bold'>Final Delivery</span>
                  </NavLink>
                </NavItem>
              </Nav>

              <TabContent activeTab={active}>
                <TabPane tabId='taskactivity'>
                  <Activity tabId={active} selectedTask={selectedTask} />
                </TabPane>
                <TabPane tabId='taskrequirments'>
                  <TaskRequirements tabId={active} selectedTask={selectedTask} />
                </TabPane>
                <TabPane tabId='taskfinal'>
                  <FinalDelivery tabId={active} selectedTask={selectedTask} />
                </TabPane>
              </TabContent>
            </CardBody>
          </Card>
        </Col>
        <Col lg={4}>
          <Card className={'font-small-4 p-0'}>
            <CardBody>
              <CardHeader className={'d-flex justify-content-between align-items-center p-0 mb-1'}>
                <h5>Task Details</h5>
                <Badge color={'light-primary'}>{parseInt(selectedTask.taskstatus) === 3 ? 'Completed' : "Inprogress"}</Badge>
              </CardHeader>
              <CardBody className={'p-0'}>
                <p>{selectedTask.servicename}</p>
                <li className='mb-75 row'>
                  <span className='fw-bolder col-6'>Date:</span>
                  <span className='col-6 text-end'>{dateFormat(selectedTask.enddate)}</span>
                </li>
                <li className='mb-75 row'>
                  <span className='fw-bolder col-12'>Users:</span>                 
                </li>
                <li className='mb-75 row'>                  
                  <span className='col-12 text-start'>
                    {selectedTask.assigeduser?.map((user, k) => {
                      return (
                        <Badge className={'ms-25'} key={k} color={'light-primary'}>{JSON.parse(user)?.name?.split(' ')[0]}</Badge>
                      )
                    })}
                  </span>
                </li>

              </CardBody>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </Fragment >
  )
}

export default UserInfoCard
