// ** React Imports
import { useEffect, useState, Fragment } from 'react'
import { useParams, Link } from 'react-router-dom'

// ** Store & Actions
import { getTask } from '../store'
import { useSelector, useDispatch } from 'react-redux'

// ** Reactstrap Imports
import { Row, Col, Alert } from 'reactstrap'

// ** User View Components
import UserInfoCard from './UserInfoCard'

// ** Styles
import '@styles/react/apps/app-users.scss'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
import { orgUserId } from '@src/helper/sassHelper'
const userId = orgUserId()
const TaskView = () => {
  // ** Store Vars
  const store = useSelector(state => state.task)
  const dispatch = useDispatch()

  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  // ** Hooks
  const { id } = useParams()

  // ** Get suer on mount
  useEffect(async () => {
    if (id !== null) {
      setBlock(true)
      await dispatch(getTask({id, userId}))
    }
    setBlock(false)

  }, [dispatch, id])


  return store.selectedTask !== null && store.selectedTask !== undefined ? (
    <UILoader blocking={block} loader={<Loader />}>
      <div className='app-user-view'>
        <Row>
          <Col xl='12' lg='12' >
            <UserInfoCard selectedTask={store.selectedTask} />
          </Col>
        </Row>
      </div>
    </UILoader>
  ) : (
    <UILoader blocking={block} loader={<Loader />}>
      {/* <Alert color='danger'>
        <h4 className='alert-heading'>Task not found</h4>
        <div className='alert-body'>
          Task with id: {id} doesn't exist. Check list of all Tasks: <Link to='/task/list'>Tasks List</Link>
        </div>
      </Alert> */}
    </UILoader>
  )
}
export default TaskView
